
import { defineComponent } from "vue";
import {
  IonPage,
  IonHeader,
  IonToolbar,
  IonTitle,
  IonContent,
  IonCard,
  IonCardHeader,
  IonCardTitle,
  IonCardContent,
  IonProgressBar,
  IonFooter,
  IonButton,
  useIonRouter,
} from "@ionic/vue";
import LetterSort from "@/components/Modes/LetterSort.vue";
import TypeMode from "@/components/Modes/TypeMode.vue";
import QuizMode from "@/components/Modes/QuizMode.vue";

import { Vocable } from "@/data/vocable";
import { useRoute } from "vue-router";
import { getBox } from "@/data/box";
import { getDueVocables, giveAnswer } from "@/data/learning";

export default defineComponent({
  name: "PractisePage",
  components: {
    LetterSort,
    TypeMode,
    QuizMode,
    IonHeader,
    IonToolbar,
    IonTitle,
    IonContent,
    IonPage,
    IonCard,
    IonCardHeader,
    IonCardTitle,
    IonCardContent,
    IonProgressBar,
    IonFooter,
    IonButton,
  },
  setup() {
    const ionRouter = useIonRouter();

    const route = useRoute();
    const id = route.params.id as string;
    return { ionRouter, id };
  },
  data() {
    return {
      words: [] as Vocable[],
      index: 0,
      activeWordId: "",
      correct: 0,
      wrong: 0,
      done: false,
      activeMode: 'LetterSort',
    };
  },
  computed: {
    currentWord: function (): Vocable | null {
      for (let i = 0; i < this.words.length; i++) {
        if (this.words[i].id === this.activeWordId) {
          return this.words[i];
        }
      }

      return {
        id: "",
        native: "",
        foreign: "",
      } as Vocable;
    },
    progress: function (): number {
      return this.index / this.words.length;
    },
  },
  async mounted() {
    if (this.id) {
      const box = await getBox(this.id);
      if (box) {
        this.words = box.vocables;
      }
    } else {
      this.words = await getDueVocables();
      if (this.words.length === 0) {
        this.done = true;
        return;
      }
    }

    this.restart();
  },
  methods: {
    nextWord() {
      this.index++;
      if (this.index > this.words.length - 1) {
        this.done = true;
      } else {
        this.activeWordId = this.words[this.index].id;
      }

      this.chooseMode();
    },
    async correctAnswer() {
      if (this.id == undefined) {
        if (this.currentWord) {
          await giveAnswer(this.currentWord, true);
        }
      }

      this.correct++;
      this.nextWord();
    },
    async wrongAnswer() {
      if (this.id == undefined) {
        if (this.currentWord) {
          await giveAnswer(this.currentWord, false);
        }
      }

      this.wrong++;

      this.nextWord();
    },
    chooseMode() {
      const rand = Math.random();
      
      if (rand < 0.5 ) {
        this.activeMode ='LetterSort';

        if (this.words[this.index].foreign.length > 14) {
          this.activeMode = Math.random() < 0.5 ? 'TypeMode' : 'QuizMode';
        }
      } else if (rand < 0.9) {
        this.activeMode = 'QuizMode';
      } else {
        this.activeMode = 'TypeMode';
      }
    },
    restart() {
      this.words.sort(() => Math.random() - 0.5);
      this.index = 0;
      this.activeWordId = this.words[this.index].id;
      this.correct = 0;
      this.wrong = 0;
      this.done = false;
      this.chooseMode();
    },
  },
});
