
import { defineComponent } from "vue";
import {
  IonGrid,
  IonRow,
  IonCol,
  IonButton,
  IonLabel,
  IonItem,
  alertController,
} from "@ionic/vue";
import { getTranslation } from "@/main";

export default defineComponent({
  name: "QuizMode",
  components: {
    IonGrid,
    IonRow,
    IonCol,
    IonButton,
    IonLabel,
    IonItem,
  },
  emits: ["correct", "wrong"],
  props: {
    word: {
      type: String,
      required: true,
    },
    solution: {
      type: String,
      required: true,
    },
    answers: {
      type: Array,
      required: true,
    },
  },
  data() {
    return {
      options: [] as string[],
    };
  },
  mounted() {
    this.options = [...this.$props.answers as string[]];
    this.options.sort(() => Math.random() - 0.5);
  },
  methods: {
    async checkAnswer(answer: string) {
      const a = answer.toLowerCase().trim();
      const s = this.$props.solution.toLowerCase().trim();

      if (a === s) {
        this.$emit("correct");
      } else {
        const alert = await alertController.create({
          header: getTranslation().t("Wrong"),
          message: getTranslation().t("The correct answer is <b>x</b>", { x: this.$props.solution }) ,
          buttons: [
            {
              text: getTranslation().t("Okay"),
              role: "confirm",
              handler: () => {
                this.$emit("wrong");
              },
            },
          ],
        });
        await alert.present();
      }
    },
  },
});
