
import { defineComponent } from "vue";
import {
  IonGrid,
  IonRow,
  IonCol,
  IonButton,
  IonLabel,
  IonItem,
  IonInput,
  alertController,
} from "@ionic/vue";
import { getTranslation } from "@/main";

export default defineComponent({
  name: "TypeMode",
  components: {
    IonGrid,
    IonRow,
    IonCol,
    IonButton,
    IonLabel,
    IonItem,
    IonInput
  },
  emits: ["correct", "wrong"],
  props: {
    word: {
      type: String,
      required: true,
    },
    solution: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      answer: ''
    };
  },
  methods: {
    async checkAnswer() {
      const a = this.answer.toLowerCase().trim();
      const s = this.$props.solution.toLowerCase().trim();

      if (a === s) {
        this.$emit("correct");
      } else {
        const alert = await alertController.create({
          header: getTranslation().t("Wrong"),
          message: getTranslation().t("The correct answer is <b>x</b>", { x: this.$props.solution }) ,
          buttons: [
            {
              text: getTranslation().t("Okay"),
              role: "confirm",
              handler: () => {
                this.$emit("wrong");
              },
            },
          ],
        });
        await alert.present();
      }
    },
  },
});
